a, abbr, acronym, address, applet, article, aside, audio, b, big, blockquote, body, canvas, caption, center, cite, code, dd, del, details, dfn, div, dl, dt, em, embed, fieldset, figcaption, figure, footer, form, h1, h2, h3, h4, h5, h6, header, hgroup, html, i, iframe, img, ins, kbd, label, legend, li, mark, menu, nav, object, ol, output, p, pre, q, ruby, s, samp, section, small, span, strike, strong, sub, summary, sup, table, tbody, td, tfoot, th, thead, time, tr, tt, u, ul, var, video {
  border: 0;
  vertical-align: baseline;
  margin: 0;
  padding: 0;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}
blockquote:before, blockquote:after, q:before, q:after {
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

b, strong {
  font-weight: bold;
}

i, em {
  font-style: italic;
}

sup {
  vertical-align: super;
  font-size: smaller;
}

sub {
  vertical-align: sub;
  font-size: smaller;
}

*,
*:before,
*:after {
  outline: none;
  box-sizing: border-box;
}

input,
textarea,
select {
  -webkit-appearance: none;
  -moz-appearance: none;
}

@font-face {
  font-family: "Geometria";
  src: url("assets/fonts/Geometria.eot") format("eot");
  src: url("assets/fonts/Geometria.ttf") format("ttf");
  src: url("assets/fonts/Geometria.woff") format("woff");
}
.body-header {
  max-width: 1170px;
  width: 100%;
  margin-top: 40px;
  margin-left: auto;
  margin-right: auto;
}
.body-header .body-header__top {
  display: flex;
  justify-content: space-between;
}
.body-header .body-header__top .top__socials {
  display: flex;
  gap: 30px;
}
.body-header .body-header__top .top__socials a svg {
  --color-rect: black;
  --color-path: white;
}
.body-header .body-header__top .top__socials a svg:hover {
  stroke: black;
  stroke-width: 1px;
  --color-rect: white;
  --color-path: black;
}
.body-header .body-header__medium {
  max-width: 750px;
  width: 100%;
  margin-top: 50px;
  margin-bottom: 40px;
  margin-left: auto;
  margin-right: auto;
}
.body-header .body-header__medium p {
  font-family: Geometria;
  font-size: 15px;
  font-weight: 400;
  color: black;
}
.body-header .body-header__medium .medium__more {
  max-height: 90px;
  overflow: hidden;
}
.body-header .body-header__medium .medium__more-active {
  max-height: none;
  overflow: visible;
  transition: overflow 0.5s;
}
.body-header .body-header__medium .medium__more-info {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  gap: 10px;
  font-family: Geometria;
  font-size: 15px;
  font-weight: 500;
  color: black;
  cursor: pointer;
}
.body-header .body-header__medium .medium__more-info svg {
  --color-svg: white;
  --color-circle: black;
  --color-firstPath: black;
  --color-secondPath: black;
}
.body-header .body-header__medium .medium__more-info:hover svg {
  --color-svg: black;
  --color-circle: white;
  --color-firstPath: white;
  --color-secondPath: white;
}
.body-header .body-header__medium .medium__more-info-active svg {
  transform: rotate(180deg);
}
.body-header .body-header__bottom {
  display: flex;
  justify-content: space-between;
  padding-top: 20px;
  border-top: 1px solid #000000;
}
.body-header .body-header__bottom p, .body-header .body-header__bottom a {
  font-family: Geometria;
  font-size: 18px;
  font-weight: 400;
  color: black;
  text-decoration: none;
}
.body-header .body-header__bottom a:hover {
  margin-bottom: 15px;
  border-bottom: 1px solid #000000;
}

.body-main {
  max-width: 1170px;
  width: 100%;
  position: relative;
  margin-top: 50px;
  margin-bottom: 25px;
  margin-left: auto;
  margin-right: auto;
}
.body-main .card__portfolio {
  display: inline-block;
  position: relative;
}
.body-main .card__portfolio .portfolio__image {
  width: inherit;
  height: inherit;
}
.body-main .card__portfolio .portfolio__name {
  display: none;
  position: absolute;
  width: inherit;
  height: inherit;
  font-family: Geometria;
  font-size: 20px;
  font-weight: 700;
  color: white;
  z-index: 999;
}
.body-main .card__portfolio:hover {
  background-color: black;
}
.body-main .card__portfolio:hover .portfolio__name {
  display: flex;
  align-items: center;
  justify-content: center;
}
.body-main .card__portfolio:hover .portfolio__image {
  opacity: 0.55;
}
