  width: 417px;
  height: 488px;
}
.body-main .card__portfolio:nth-child(3) {
  width: 328px;
  height: 488px;
}
.body-main .card__portfolio:nth-child(5) {
  width: 417px;
  height: 488px;
}
.body-main .card__portfolio:nth-child(7) {
  width: 583px;
  height: 671px;
}
.body-main .card__portfolio:nth-child(9) {
  width: 583px;
  height: 671px;
}
.body-main .card__portfolio:nth-child(11) {
  width: 740px;
  height: 494px;
}
.body-main .card__portfolio:nth-child(13) {
  width: 426px;
  height: 494px;
}
.body-main .card__portfolio:nth-child(15) {
  width: 506px;
  height: 590px;
}
.body-main .card__portfolio:nth-child(17) {
  width: 660px;
  height: 590px;
}
.body-main .card__popup {
  display: none;
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
  background-color: rgba(0, 0, 0, 0.55);
  z-index: 2;
}
.body-main .card__popup .popup__container {
  width: 100%;
  height: 100%;
}
.body-main .card__popup .popup__container .container__body {
  position: relative;
  height: 700px;
  margin-top: 25px;
  margin-bottom: 25px;
  margin-left: 25px;
  margin-right: 25px;
  background-color: white;
  border-radius: 10px;
  text-align: center;
}
.body-main .card__popup .popup__container .container__body .body__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 15px;
  padding-bottom: 20px;
  padding-left: 25px;
  padding-right: 25px;
}
.body-main .card__popup .popup__container .container__body .body__top .top__name {
  font-family: Geometria;
  font-size: 18px;
  font-weight: 500;
}
.body-main .card__popup .popup__container .container__body .body__medium {
  display: flex;
  justify-content: center;
}
.body-main .card__popup .popup__container .container__body .body__medium .medium-button-prev, .body-main .card__popup .popup__container .container__body .body__medium .medium-button-next {
  cursor: pointer;
  display: flex;
  align-items: center;
  z-index: 9999;
}
.body-main .card__popup .popup__container .container__body .body__medium .medium__swiper {
  display: flex;
  width: 925px;
  margin-left: 50px;
  margin-right: 50px;
}
.body-main .card__popup .popup__container .container__body .body__medium .medium__swiper .swiper__body .body__slides .slides__slide {
  width: 300px !important;
}
.body-main .card__popup .popup__container .container__body .body__bottom {
  padding-top: 20px;
  padding-bottom: 15px;
  padding-left: 25px;
  padding-right: 25px;
  font-family: Geometria;
  font-size: 15px;
  font-weight: 400;
}
.body-main .popup-active {
  display: flex;
}

.body-footer {
  display: flex;
  justify-content: center;
  font-family: Geometria;
  font-size: 15px;
  font-weight: 400;
  margin-bottom: 50px;
}